import { http } from '@/http';
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { defineComponent, onMounted, ref } from 'vue';
export default defineComponent({
    name: 'MyInvite',
    setup() {
        const inviteMsgList = ref([]);
        const msgTplList = ref([
            {
                id: 0,
                rcdNote: null,
                title: 'title2',
                subTitle: 'sub2',
                content: '33',
                idx: 1,
                ctime: ''
            },
            {
                id: 0,
                rcdNote: null,
                title: 'title2',
                subTitle: 'sub2',
                content: '33',
                idx: 1,
                ctime: ''
            }
        ]);
        const showTplList = ref(false);
        const getMsgTplList = () => {
            http
                .post('/hbSeller/sellerInvite/msgTplList', {}, false)
                .then((res) => {
                if (res.code === '200') {
                    msgTplList.value = res.data.msgTplList;
                }
                else {
                    Toast.fail(res.msg);
                }
            })
                .catch((err) => {
                console.log(err);
            });
        };
        const getInviteMsgList = () => {
            http
                .post('/hbSeller/sellerInvite/inviteMsgList', {}, false)
                .then((res) => {
                if (res.code === '200') {
                    inviteMsgList.value = res.data;
                }
                else {
                    Toast.fail(res.msg);
                }
            })
                .catch((err) => {
                console.log(err);
            });
        };
        onMounted(() => {
            getInviteMsgList();
            getMsgTplList();
        });
        return {
            inviteMsgList,
            msgTplList,
            showTplList
        };
    }
});
